import React from 'react'
import {
    Nav
  } from "rsuite";

const Burger = ({active, onSelect, ...props}) => {
  
  return (
    <Nav {...props} activeKey={active} onSelect={onSelect} >
      <Nav.Item style={{display: "block", width: "100%"}} eventKey="main" ></Nav.Item>
      <Nav.Item style={{display: "block", width: "100%"}} eventKey="home" ></Nav.Item>
      <Nav.Item style={{display: "block", width: "100%"}} eventKey="roof" ></Nav.Item>
      <Nav.Item style={{display: "block", width: "100%"}} eventKey="clinkers" ></Nav.Item>
      <Nav.Item style={{display: "block", width: "100%"}} eventKey="border" ></Nav.Item>
    </Nav>
  )
}

export default Burger 